body {
  box-sizing: border-box;
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
}

.people {
  --columns: 2;
  grid-template-columns: repeat(var(--columns), 200px);
  justify-content: center;
  align-items: center;
  gap: 30px;
  padding: 30px 35px;
  display: grid;
}

@media (width >= 500px) {
  .people {
    --columns: 2;
  }
}

@media (width >= 1000px) {
  .people {
    --columns: 4;
  }
}

.person {
  text-align: center;
  border-radius: 15px;
  flex-direction: column;
  align-items: center;
  min-height: 240px;
  padding: 10px;
  transition: all .3s;
  display: flex;
}

.person:hover {
  background-color: #eee;
  transform: translateY(-5px);
}

.photo {
  position: relative;
}

.person__photo {
  object-fit: cover;
  border-radius: 50%;
  width: 200px;
  height: 200px;
  margin-bottom: 8px;
  transition: border-radius 1s;
}

.person:hover .person__photo {
  border-radius: 20%;
}

.person__rank {
  color: #8b0000;
  background-color: #fff;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;
  font-size: 16px;
  font-weight: 700;
  display: flex;
  position: absolute;
  top: 84px;
  right: 20px;
}

.person__extra {
  opacity: 0;
  justify-content: center;
  gap: 4px;
  height: 40px;
  margin-top: 8px;
  transition: all .3s;
  display: flex;
  transform: translateY(10px);
}

.person__sport, .person__pay {
  color: #000;
  margin: 0;
  font-size: 18px;
  font-weight: 400;
  transition: font-size 1s;
}

.person__name {
  color: #2f4f4f;
  margin: 0;
  font-size: 20px;
  font-weight: 700;
  transition: font-size 1s;
}

.person:hover .person__name {
  font-size: 24px;
}

.person:hover .person__extra {
  opacity: 1;
  transform: translateY(0);
}

.person:hover .person__sport, .person:hover .person__pay {
  font-size: 21.6px;
}

.person__sport:after {
  content: ", ";
}

.person__pay:before {
  content: "$";
}

.person__pay:after {
  content: "M";
}
